import { setLocale } from "yup";

const ptBR = {
  mixed: {
    required: ({ path }) => `${path}  requerido`,
    notType: ({ path }) => `${path}  invlido`,
  },
  string: {
    max: ({ path, max }) => `${path} precisa ter no mximo ${max} caracteres`,
  },
  number: {
    min: ({ path, min }) => `${path} precisa ser no mnimo ${min}`,
  },
  array: {
    min: ({ path, min }) => `${path} precisa ter no mnimo ${min} item`,
  },
};

setLocale(ptBR);

export * from "yup";
